export interface ExaminationProgressReportContentInterface {
  key: ExaminationProgressReportContentKeyInterface;
  title: string;
  status: boolean;
}

export const examinationProgressIsChangeAllowed = (
  key: ExaminationProgressReportContentKeyInterface
): boolean => {
  if (
    key === ExaminationProgressReportContentKeyInterface.COURSER_NAME ||
    key === ExaminationProgressReportContentKeyInterface.COURSE_CODE ||
    key === ExaminationProgressReportContentKeyInterface.PERIOD ||
    key === ExaminationProgressReportContentKeyInterface.STATUS
  ) {
    return false;
  }
  return true;
};

