export const displayStatus = (status: string): string => {
  if (status === "Done") {
    return "Completed";
  }
  if (status === "Editing") {
    return "In exam";
  }
  return status;
};

export interface examProgressStateInterface {
  course_name: string;
  course_code: string;
  description: string;
  school_levels: string[];
  course_id: string;
  exam: ExamItemResponse | null;
  preparation_status: string;
}

export const FC_reportDoneExamList = (
  allCourses: course[],
  allExams: ExamItemResponse[],
  doneExams: ExamDoneAndRemainingByPeriodInterface[]
): examProgressStateInterface[] => {
  let result: examProgressStateInterface[] = [],
    ids: string[] = [];
  for (const key in allExams) {
    if (Object.prototype.hasOwnProperty.call(allExams, key)) {
      if (ids.includes(allExams[key].id)) continue;
      ids.push(allExams[key].id);
      let courser = allCourses.find(
        (item) => item.course_id === allExams[key].course
      );
      if (courser) {
        let doneExam = doneExams.find(
          (item) =>
            item.course === allExams[key].course &&
            item.period === allExams[key].period
        );
        result.push({
          course_name: courser.course_name,
          course_code: courser.course_code,
          description: courser.description,
          school_levels: courser.school_levels,
          course_id: courser.course_id,
          exam: allExams[key],
          preparation_status: doneExam ? doneExam.exam_status : "remaining",
        });
      }
    }
  }
  return result;
};

export const FC_reportDoneExamListFilter = (
  thisList: examProgressStateInterface[],
  thisValue: "all" | "Done" | "Active"
): examProgressStateInterface[] => {
  if (thisValue === "all") {
    return thisList;
  } else {
    return thisList.filter((item) => item.preparation_status === thisValue);
  }
};

export const searchExamData = (
  objList: examProgressStateInterface[],
  text: string
): examProgressStateInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "course_name" ||
        prop === "course_code" ||
        prop === "preparation_status"
      ) {
        flag =
          product[prop].toString().toLowerCase().indexOf(text.toLowerCase()) >
          -1;
      }
      if (flag) break;
    }
    return flag;
  });
};

type DoneExamListProps = {
  loading: boolean;
  examsListData: ExamItemResponse[] | null;
  courseListData: course[] | null;
  thisPeriod: SessionPlanInterface[];
  doneExamsData: ExamDoneAndRemainingByPeriodInterface[] | null;

  // for filter purpose =============================
  filterExamProgress: "all" | "Done" | "Active";
};

const DoneExamList: React.FC<DoneExamListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  const [isSorted, setIsSorted] = useState<boolean>(false);
  const [sortProperty, setSortProperty] = useState<string>("");
  const [sortOrder, setSortOrder] = useState<"ASC" | "DESC">("ASC");

  const setSort = (thisProperty: string) => {
    if (sortProperty === thisProperty && sortOrder === "ASC") {
      setSortOrder("DESC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    } else {
      setSortOrder("ASC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    }
  };

  return (
    <Fragment>
      <div className="bg-white my-4">
        <div className="w-full px-2 py-2">
          <input
            onChange={(e) => setSearchInput(e.target.value)}
            type="text"
            value={searchInput}
            placeholder="Search..."
            className="w-full bg-gray-100 py-2 px-3 rounded"
            disabled={
              props.courseListData &&
              props.examsListData &&
              props.doneExamsData &&
              FC_reportDoneExamListFilter(
                FC_reportDoneExamList(
                  props.courseListData,
                  props.examsListData,
                  props.doneExamsData
                ),
                props.filterExamProgress
              ).length <= 0
                ? true
                : false
            }
          />
        </div>
        <table className="w-full shadow" id="report_content">
          <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
            <tr>
              <td className="px-2 py-2">#</td>
              <td
                className="px-2 py-2 cursor-pointer"
                onClick={() => setSort("course_name")}
              >
                <div className="flex items-center gap-1">
                  Course <BiSort />
                </div>
              </td>
              <td className="px-2 py-2">Exam date</td>
              <td className="px-2 py-2">Exam time</td>
              <td className="px-2 py-2">Exam duration</td>
              <td className="px-2 py-2">Exam Period</td>
              <td
                className="px-2 py-2 cursor-pointer"
                onClick={() => setSort("preparation_status")}
              >
                <div className="flex items-center gap-1">
                  Exam Status <BiSort />
                </div>
              </td>
            </tr>
          </thead>
          <tbody>
            {props.courseListData === null || props.loading ? (
              <tr>
                <td colSpan={7} className="px-2 py-2">
                  <Loading />
                </td>
              </tr>
            ) : props.examsListData &&
              props.doneExamsData &&
              FC_reportDoneExamListFilter(
                FC_reportDoneExamList(
                  props.courseListData,
                  props.examsListData,
                  props.doneExamsData
                ),
                props.filterExamProgress
              ).length <= 0 ? (
              <tr>
                <td colSpan={7} className="px-2 py-2 text-center">
                  No Exam list available!
                </td>
              </tr>
            ) : (
              props.examsListData &&
              props.doneExamsData &&
              sortThisArray(
                isSorted,
                searchExamData(
                  FC_reportDoneExamListFilter(
                    FC_reportDoneExamList(
                      props.courseListData,
                      props.examsListData,
                      props.doneExamsData
                    ),
                    props.filterExamProgress
                  ),
                  searchInput
                ),
                sortProperty,
                sortOrder
              ).map((item, i) => (
                <tr
                  key={i + 1}
                  className={`${
                    item.preparation_status === "Done"
                      ? "bg-blue-50"
                      : "bg-white"
                  } text-gray-600 hover:bg-blue-100 hover:text-blue-700 border-b border-gray-300 hover:border-blue-500 text-sm`}
                >
                  <td className="px-2 py-2">{i + 1}</td>
                  <td className="px-2 py-2">
                    {capitalizeFirstLetter(item.course_name)} (
                    <span className="font-semibold">{item.course_code}</span>)
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? DATE(item.exam.exam_date) : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? item.exam.start_time : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? item.exam!.duration + " min" : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam
                      ? getPeriodById(props.thisPeriod, item.exam.period)
                        ? getPeriodById(props.thisPeriod, item.exam.period)!
                            .session_plan_name
                        : "-"
                      : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {capitalizeFirstLetter(
                      displayStatus(item.preparation_status)
                    )}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </Fragment>
  );
};

export default React.memo(DoneExamList);
