import React, { Fragment, useState } from "react";
import { connect } from "react-redux";
import Loading from "../../../components/Loading/Loading";
import {
  course,
  CandidatesFailedSucceeded,
  ReportUserExamStatus,
  SessionPlanInterface,
} from "../../../interfaces";
import { ExamItemResponse } from "../../../interfaces/prepare_exam";
import { StoreState } from "../../../reducers";
import { ExamReportReducerType } from "../../../reducers/exam_report";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import { DATE, timestampToDate } from "../../../utils/functions";
import {
  candidatePerfomranceStateInterface,
  controlRowsByStatus,
  FC_reportCandidatePerformanceData,
  searchCandidatePerformanceData,
} from "./CandidatePerformance";
import { displayStatus } from "./DoneExamList";
import { getPeriodById } from "./ExamReport";

export const FC_CandiadtePerformancePerDistrictFilter = (
  dataList: candidatePerfomranceStateInterface[],
  district_id: string | null = null,
  course_id: string | null = null,
  thisValue: ReportUserExamStatus | "all" = "all"
): candidatePerfomranceStateInterface[] => {
  let result: candidatePerfomranceStateInterface[] = [];
  if (thisValue === "all") {
    result = dataList;
  } else {
    result = dataList.filter((item) => item.exam_stastus === thisValue);
  }

  if (district_id) {
    result = result.filter(
      (item) =>
        item.exam.exam_location &&
        item.exam.exam_location.district_id === district_id
    );
  }
  if (course_id) {
    result = result.filter((item) => item.course_id === course_id);
  }

  // SORTING ARRAY FROM MAX TO MIN ==========================
  let swap: candidatePerfomranceStateInterface;
  for (let i = 0; i < result.length; i++) {
    for (let j = i; j < result.length; j++) {
      if (result[j].user_total_marks > result[i].user_total_marks) {
        swap = result[j];
        result[j] = result[i];
        result[i] = swap;
      }
    }
  }
  return result;
};

type CandiadtePerformancePerDistrictProps = {
  loading: boolean;
  examsListData: ExamItemResponse[] | null;
  courseListData: course[] | null;
  thisPeriod: SessionPlanInterface[];
  candidatePerformanceData: CandidatesFailedSucceeded[] | null;

  // redux props ============
  adminExamReport: ExamReportReducerType;
  // FOR APPLICANT PERFORMANCE PROPS
  filterApplicantPerformance: ReportUserExamStatus | "all";
};

const CandiadtePerformancePerDistrict: React.FC<
  CandiadtePerformancePerDistrictProps
> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");

  return (
    <Fragment>
      <div className="bg-white my-4">
        <div className="w-full px-2 py-2">
          <input
            onChange={(e) => setSearchInput(e.target.value)}
            type="text"
            value={searchInput}
            placeholder="Search..."
            className="w-full bg-gray-100 py-2 px-3 rounded"
            disabled={
              props.courseListData &&
              props.candidatePerformanceData &&
              FC_CandiadtePerformancePerDistrictFilter(
                FC_reportCandidatePerformanceData(
                  props.courseListData,
                  props.candidatePerformanceData,
                  props.adminExamReport.reportCourseId
                    ? props.adminExamReport.reportCourseId.course_id
                    : "all"
                ),
                props.adminExamReport.reportDistrictId
                  ? props.adminExamReport.reportDistrictId.district_id
                  : null,
                props.adminExamReport.reportCourseId
                  ? props.adminExamReport.reportCourseId.course_id
                  : null,
                props.filterApplicantPerformance
              ).length <= 0
                ? true
                : false
            }
          />
        </div>
        <div className="w-full overflow-x-auto">
          <table className="w-full shadow" id="report_content">
            <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
              <tr>
                <td className="px-2 py-2">#</td>
                <td className="px-2 py-2">Applicants</td>
                <td className="px-2 py-2">National ID</td>
                <td className="px-2 py-2">Email</td>
                <td className="px-2 py-2">Phone</td>
                <td className="px-2 py-2">
                  <div className="flex items-center gap-1">Exam</div>
                </td>
                <td className="px-2 py-2">Exam period</td>
                <td className="px-2 py-2">Exam date</td>
                <td className="px-2 py-2">Exam start time</td>
                <td className="px-2 py-2">Exam end time</td>
                <td className="px-2 py-2">
                  <div className="flex items-center gap-1">Marks</div>
                </td>
                <td className="px-2 py-2">Status</td>
              </tr>
            </thead>
            <tbody>
              {props.courseListData === null || props.loading ? (
                <tr>
                  <td colSpan={12} className="px-2 py-2">
                    <Loading />
                  </td>
                </tr>
              ) : props.candidatePerformanceData &&
                FC_CandiadtePerformancePerDistrictFilter(
                  FC_reportCandidatePerformanceData(
                    props.courseListData,
                    props.candidatePerformanceData,
                    props.adminExamReport.reportCourseId
                      ? props.adminExamReport.reportCourseId.course_id
                      : "all"
                  ),
                  props.adminExamReport.reportDistrictId
                    ? props.adminExamReport.reportDistrictId.district_id
                    : null,
                  props.adminExamReport.reportCourseId
                    ? props.adminExamReport.reportCourseId.course_id
                    : null,
                  props.filterApplicantPerformance
                ).length <= 0 ? (
                <tr>
                  <td colSpan={12} className="px-2 py-2 text-center">
                    No applicant list available!
                  </td>
                </tr>
              ) : (
                props.candidatePerformanceData &&
                searchCandidatePerformanceData(
                  FC_CandiadtePerformancePerDistrictFilter(
                    FC_reportCandidatePerformanceData(
                      props.courseListData,
                      props.candidatePerformanceData,
                      props.adminExamReport.reportCourseId
                        ? props.adminExamReport.reportCourseId.course_id
                        : "all"
                    ),
                    props.adminExamReport.reportDistrictId
                      ? props.adminExamReport.reportDistrictId.district_id
                      : null,
                    props.adminExamReport.reportCourseId
                      ? props.adminExamReport.reportCourseId.course_id
                      : null,
                    props.filterApplicantPerformance
                  ).slice(0, 30),
                  searchInput
                ).map((item, i) => (
                  <tr
                    key={i + 1}
                    className={`${controlRowsByStatus(
                      item.exam_stastus
                    )} text-gray-600 hover:bg-blue-100 hover:text-blue-700 text-sm`}
                  >
                    <td className="px-2 py-2">{i + 1}</td>
                    <td className="px-2 py-2">
                      {capitalizeFirstLetter(item.user_name)}
                    </td>
                    <td className="px-2 py-2">{item.national_id}</td>
                    <td className="px-2 py-2">{item.user_email}</td>
                    <td className="px-2 py-2">{item.user_phone}</td>
                    <td className="px-2 py-2">
                      {capitalizeFirstLetter(item.course_name)} (
                      <span className="font-semibold">{item.course_code}</span>)
                    </td>
                    <td className="px-2 py-2">
                      {item.exam
                        ? getPeriodById(props.thisPeriod, item.exam.period)
                          ? getPeriodById(props.thisPeriod, item.exam.period)!
                              .session_plan_name
                          : "-"
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam.exam_date ? DATE(item.exam.exam_date) : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam.start_time
                        ? timestampToDate(item.exam.start_time).timeAMPM
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam.end_time
                        ? timestampToDate(item.exam.end_time).timeAMPM
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam_stastus !== ReportUserExamStatus.Editing
                        ? Number(item.user_total_marks.toFixed(1)) + "%"
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {capitalizeFirstLetter(displayStatus(item.exam_stastus))}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  adminExamReport,
}: StoreState): {
  adminExamReport: ExamReportReducerType;
} => {
  return {
    adminExamReport,
  };
};

export default React.memo(
  connect(mapStateToProps, {})(CandiadtePerformancePerDistrict)
);
