import React, { Fragment, useState } from "react";
import { connect } from "react-redux";
// import { BiSort } from "react-icons/bi";
import Loading from "../../../components/Loading/Loading";
import NoResult, {
  NoResultThemes,
} from "../../../components/NoResult/NoResult";
import {
  course,
  CandidatesFailedSucceeded,
  ExamDoneAndRemainingByPeriodInterface,
  ReportUserExamStatus,
  SessionPlanInterface,
} from "../../../interfaces";
import { ExamItemResponse } from "../../../interfaces/prepare_exam";
import { StoreState } from "../../../reducers";
import { ExamReportReducerType } from "../../../reducers/exam_report";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import { DATE } from "../../../utils/functions";
import search from "../../../utils/search";
import { getPeriodById } from "./ExamReport";

export interface examPerformanceStateInterface {
  course_name: string;
  course_code: string;
  description: string;
  school_levels: string[];
  applicants: number;
  course_id: string;
  pass_applicant: number;
  failed_applicant: number;
  exam: ExamItemResponse;
  user_total_marks: number;
}

export const FC_ExamPerformancePerDistrictPropsData = (
  allCourses: course[],
  allExams: ExamItemResponse[],
  examsData: ExamDoneAndRemainingByPeriodInterface[],
  candidatePerformanceData: CandidatesFailedSucceeded[],
  district_id: string
): examPerformanceStateInterface[] => {
  let pass: number = 0,
    failed: number = 0,
    result: examPerformanceStateInterface[] = [],
    ids: string[] = [],
    exam_ids: string[] = [],
    doneExamsData: ExamDoneAndRemainingByPeriodInterface[] = examsData.filter(
      (item) => item.exam_status.toLocaleLowerCase() === "done"
    );

  for (const key in doneExamsData) {
    if (Object.prototype.hasOwnProperty.call(doneExamsData, key)) {
      if (exam_ids.includes(doneExamsData[key].id)) continue;
      exam_ids.push(doneExamsData[key].id);
      let courser = allCourses.find(
        (item) => item.course_id === doneExamsData[key].course
      );
      let _marks: number = 0;

      if (courser) {
        // CALCURATING SUM OF ALL CANDIDATES MARKS BY EXAM ID=================================
        let candidatesByExam: CandidatesFailedSucceeded[] =
          candidatePerformanceData.filter(
            (item) =>
              item.course === doneExamsData[key].course &&
              item.exam_location &&
              item.exam_location.district_id === district_id
          );
        for (const _key in candidatesByExam) {
          if (Object.prototype.hasOwnProperty.call(candidatesByExam, _key)) {
            if (ids.includes(candidatesByExam[_key].user.user_id)) continue;
            ids.push(candidatesByExam[_key].user.user_id);
            _marks += candidatesByExam[_key].total_marks;
            if (
              candidatesByExam[_key].user_status !==
                ReportUserExamStatus.Editing &&
              candidatesByExam[_key].total_marks >= 50
            ) {
              pass += 1;
            } else if (
              candidatesByExam[_key].user_status !==
              ReportUserExamStatus.Editing
            ) {
              failed += 1;
            }
          }
        }
        let exam = allExams.find(
          (item) =>
            item.course === doneExamsData[key].course &&
            item.period === doneExamsData[key].period
        );
        if (exam) {
          result.push({
            course_name: courser.course_name,
            course_code: courser.course_code,
            description: courser.description,
            school_levels: courser.school_levels,
            course_id: courser.course_id,
            exam: exam,
            pass_applicant: pass,
            failed_applicant: failed,
            applicants: ids.length,
            user_total_marks: _marks ? _marks / ids.length : _marks, //candidatesByExam.length
          });
        }
        _marks = 0;
        ids = [];
      }
    }
    exam_ids = [];
  }

  // SORTING ARRAY FROM MAX TO MIN ==========================
  let swap: examPerformanceStateInterface;
  for (let i = 0; i < result.length; i++) {
    for (let j = i; j < result.length; j++) {
      if (result[j].user_total_marks > result[i].user_total_marks) {
        swap = result[j];
        result[j] = result[i];
        result[i] = swap;
      }
    }
  }
  return result;
};

type ExamPerformancePerDistrictProps = {
  loading: boolean;
  examsListData: ExamItemResponse[] | null;
  doneExamsData: ExamDoneAndRemainingByPeriodInterface[] | null;
  courseListData: course[] | null;
  thisPeriod: SessionPlanInterface[];
  candidatePerformanceData: CandidatesFailedSucceeded[] | null;
  thisDistrict: { district_id: string; district_name: string };
  adminExamReport: ExamReportReducerType;
};

const ExamPerformancePerDistrict: React.FC<ExamPerformancePerDistrictProps> = (
  props
) => {
  const [searchInput, setSearchInput] = useState<string>("");

  return (
    <Fragment>
      <div className="bg-white my-4 max-w-full overflow-x-auto">
        <div className="w-full px-2 py-2">
          <input
            onChange={(e) => setSearchInput(e.target.value)}
            type="text"
            value={searchInput}
            placeholder="Search..."
            className="w-full bg-gray-100 py-2 px-3 rounded"
            disabled={
              props.courseListData &&
              props.examsListData &&
              props.doneExamsData &&
              props.candidatePerformanceData &&
              props.adminExamReport.reportDistrictId &&
              FC_ExamPerformancePerDistrictPropsData(
                props.courseListData,
                props.examsListData,
                props.doneExamsData,
                props.candidatePerformanceData,
                props.adminExamReport.reportDistrictId.district_id
              ).slice(0, 30).length <= 0
                ? true
                : false
            }
          />
        </div>
        <table className="w-full shadow" id="report_content">
          <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
            <tr>
              <td className="px-2 py-2">#</td>
              <td
                className="px-2 py-2 cursor-pointer"
                // onClick={() => setSort("course_name")}
              >
                <div className="flex items-center gap-1">
                  Course
                  {/* <BiSort /> */}
                </div>
              </td>
              <td className="px-2 py-2">Exam date</td>
              <td className="px-2 py-2">Exam time</td>
              <td className="px-2 py-2">Exam duration</td>
              <td className="px-2 py-2">Exam Period</td>
              <td className="px-2 py-2">Pass Applicants</td>
              <td className="px-2 py-2">Failed Applicants</td>
              <td
                className="px-2 py-2 cursor-pointer"
                // onClick={() => setSort("user_total_marks")}
              >
                <div className="flex items-center gap-1">
                  Percentage
                  {/* <BiSort /> */}
                </div>
              </td>
            </tr>
          </thead>
          <tbody>
            {props.courseListData === null || props.loading ? (
              <tr>
                <td colSpan={9} className="px-2 py-2">
                  <Loading />
                </td>
              </tr>
            ) : props.examsListData &&
              props.doneExamsData &&
              props.candidatePerformanceData &&
              props.adminExamReport.reportDistrictId &&
              FC_ExamPerformancePerDistrictPropsData(
                props.courseListData,
                props.examsListData,
                props.doneExamsData,
                props.candidatePerformanceData,
                props.adminExamReport.reportDistrictId.district_id
              ).length <= 0 ? (
              <tr>
                <td colSpan={9} className="px-2 py-2 text-center">
                  <NoResult
                    theme={NoResultThemes.Dark}
                    value={"No Exam list available!ss"}
                  />
                </td>
              </tr>
            ) : (
              props.examsListData &&
              props.doneExamsData &&
              props.candidatePerformanceData &&
              props.adminExamReport.reportDistrictId &&
              search(
                FC_ExamPerformancePerDistrictPropsData(
                  props.courseListData,
                  props.examsListData,
                  props.doneExamsData,
                  props.candidatePerformanceData,
                  props.adminExamReport.reportDistrictId.district_id
                ).slice(0, 30),
                searchInput
              ).map((item: examPerformanceStateInterface, i) => (
                <tr
                  key={i + 1}
                  className={`bg-white text-gray-600 hover:bg-blue-100 hover:text-blue-700 border-b border-gray-300 hover:border-blue-500 text-sm`}
                >
                  <td className="px-2 py-2">{i + 1}</td>
                  <td className="px-2 py-2">
                    {capitalizeFirstLetter(item.course_name)} (
                    <span className="font-semibold">{item.course_code}</span>)
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? DATE(item.exam.exam_date) : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? item.exam.start_time : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam ? item.exam!.duration + " min" : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.exam
                      ? getPeriodById(props.thisPeriod, item.exam.period)
                        ? getPeriodById(props.thisPeriod, item.exam.period)!
                            .session_plan_name
                        : "-"
                      : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.pass_applicant !== undefined
                      ? item.pass_applicant
                      : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {item.failed_applicant !== undefined
                      ? item.failed_applicant
                      : "-"}
                  </td>
                  <td className="px-2 py-2">
                    {Number(item.user_total_marks.toFixed(1)) + "%"}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  adminExamReport,
}: StoreState): {
  adminExamReport: ExamReportReducerType;
} => {
  return {
    adminExamReport,
  };
};

export default React.memo(
  connect(mapStateToProps, {})(ExamPerformancePerDistrict)
);
