import React, { Fragment } from "react";
import { MainItem } from "../../../components/Navigation/SideNavigation";
import { ReportUserExamStatus } from "../../../interfaces";
import { displayStatus } from "./DoneExamList";
import { reportTitleType } from "./ExamReport";

type ExamReportFiltersProps = {
  reportTitle: reportTitleType;

  // FOR EXAM PREPARATION PROPS =========================================
  filterPreparation: "prepared" | "not prepared" | "all";
  setFilterPreparation: (value: "prepared" | "not prepared" | "all") => void;

  // FOR EXAMINATION PROGRESS PROPS ========================================
  filterExamProgress: "all" | "Done" | "Active";
  setFilterExamProgress: (value: "all" | "Done" | "Active") => void;

  // FOR APPLICANT PERFORMANCE PROPS
  filterApplicantPerformance: ReportUserExamStatus | "all";
  setFilterApplicantPerformance: (value: "all" | ReportUserExamStatus) => void;
};
const ExamReportFilters: React.FC<ExamReportFiltersProps> = (props) => {
  return (
    <Fragment>
      {props.reportTitle !== reportTitleType.EXAM_PERFORMANCE &&
        props.reportTitle !== reportTitleType.EXAM_DISTRICT_PERFORMANCE && (
          <div className="pt-1">
            <div className="text-sm text-gray-700 pb-2 pt-2">
              Filter by statuses:
            </div>
            {/* FILTER OF EXAM PREPARATION ================================ */}
            {props.reportTitle === reportTitleType.EXAM_PREPARATION && (
              <section>
                <div onClick={() => props.setFilterPreparation("all")}>
                  <MainItem
                    title="All"
                    count={0}
                    active={props.filterPreparation === "all"}
                  />
                </div>
                <div onClick={() => props.setFilterPreparation("prepared")}>
                  <MainItem
                    title="Prepared"
                    count={0}
                    active={props.filterPreparation === "prepared"}
                  />
                </div>
                <div onClick={() => props.setFilterPreparation("not prepared")}>
                  <MainItem
                    title="Not Prepared"
                    count={0}
                    active={props.filterPreparation === "not prepared"}
                  />
                </div>
              </section>
            )}
            {/* END FILTER OF EXAM PREPARATION ================================ */}

            {/* FILTER OF EXAMINATION PROGRESS ================================ */}
            {props.reportTitle === reportTitleType.EXAMINATION_PROGRESS && (
              <section>
                <div onClick={() => props.setFilterExamProgress("all")}>
                  <MainItem
                    title="All"
                    count={0}
                    active={props.filterExamProgress === "all"}
                  />
                </div>
                <div onClick={() => props.setFilterExamProgress("Done")}>
                  <MainItem
                    title="Completed exam"
                    count={0}
                    active={props.filterExamProgress === "Done"}
                  />
                </div>
                <div onClick={() => props.setFilterExamProgress("Active")}>
                  <MainItem
                    title="Remaining Exam"
                    count={0}
                    active={props.filterExamProgress === "Active"}
                  />
                </div>
              </section>
            )}
            {/* END FILTER OF EXAMINATION PROGRESS ================================ */}

            {/* FILTER OF APPLICANTS PERFORMANCE ================================ */}
            {(props.reportTitle === reportTitleType.APPLICANTS_PERFORMANCE ||
              props.reportTitle ===
                reportTitleType.APPLICANTS_DISTRICT_PERFORMANCE) && (
              <section>
                <div onClick={() => props.setFilterApplicantPerformance("all")}>
                  <MainItem
                    title="All"
                    count={0}
                    active={props.filterApplicantPerformance === "all"}
                  />
                </div>

                {Object.values(ReportUserExamStatus).map((item, index) => (
                  <div
                    onClick={() => props.setFilterApplicantPerformance(item)}
                    key={index + item}
                  >
                    <MainItem
                      title={displayStatus(item)}
                      count={0}
                      active={props.filterApplicantPerformance === item}
                    />
                  </div>
                ))}
              </section>
            )}
            {/* END FILTER OF APPLICANTS PERFORMANCE ================================ */}
          </div>
        )}
    </Fragment>
  );
};

export default React.memo(ExamReportFilters);
