import React, { Fragment, useEffect, useState } from "react";
import { BiExport, BiFilterAlt } from "react-icons/bi";
import { FaEdit } from "react-icons/fa";
import { connect } from "react-redux";
import {
  schoolLevelsStoreInterface,
  SetReportDistrictId,
  SetReportDistrictIdAction,
} from "../../../actions";
import { course } from "../../../interfaces/index";
import { ExamItemResponse } from "../../../interfaces/prepare_exam";
import { StoreState } from "../../../reducers";
import { ExamReportReducerType } from "../../../reducers/exam_report";
import { loadExaminationProgress } from "../../AdminDashboard/ExamDashboard/ExamDashboard";
import {
  CandidateExamPerformanceReportContentInterface,
  CandidateExamPerformanceToPrintInterface,
} from "./CandidatePerformance";
import {
  ExaminationProgressReportContentInterface,
  ExaminationProgressToPrintInterface,
} from "./DoneExamList";
import {
  examPreparationReportContentInterface,
  ExamPreparationToPrintInterface,
} from "./ExamList";
import {
  ExamPerformanceToPrintInterface,
  examPerformanceReportContentInterface,
} from "./ExamPerformance";
import { reportTitleType } from "./ExamReport";
import ExamReportModals, { ModalForTypes } from "./ExamReportModals";

type ExamReportTitleProps = {
  thisPeriod: string;
  // FOR PRINT PDF =========================
  downloading: boolean;
  dataToPrint: any[] | null;
  downlaodPDF: () => Promise<false | undefined>;
  // FOR REPORT TYPE =========================
  reportTitle: reportTitleType;
  setReportTitle: (title: reportTitleType) => void;
  // FOR GENERATE EXCEL =========================
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  setGenerateReportModal: (
    reportData: {
      reportType: "EXCEL" | "PDF";
      status: boolean;
    } | null
  ) => void;
  excelReportTitle: string;
  setExcelReportTitle: (title: string) => void;
  updateExamToPrint: () => void;
  // FOR EXCEL EXAM PREPARATION REPORT CONTENT ===============================
  examPreparationReportContent: examPreparationReportContentInterface[];
  setExamPreparationReportContent: (
    content: examPreparationReportContentInterface[]
  ) => void;
  examsToPrint:
    | ExamPreparationToPrintInterface[]
    | ExamPerformanceToPrintInterface[]
    | null;
  // FOR EXCEL EXAM PERFORMANCE REPORT CONTENT ===============================
  examPerformanceToPrint: ExamPerformanceToPrintInterface[] | null;
  examPerformanceReportContent: examPerformanceReportContentInterface[];
  setExamPerformanceReportContent: (
    value: examPerformanceReportContentInterface[]
  ) => void;

  // FOR EXCEL TOP 30 EXAM PERFORMANCE REPORT CONTENT ===============================
  exam30PerformanceToPrint: ExamPerformanceToPrintInterface[] | null;
  exam30PerformanceReportContent: examPerformanceReportContentInterface[];
  setExam30PerformanceReportContent: (
    value: examPerformanceReportContentInterface[]
  ) => void;

  // FOR EXCEL EXAMINATION PROGRESS REPORT CONTENT ===============================
  examinationProgressToPrint: ExaminationProgressToPrintInterface[] | null;
  examinationProgressReportContent: ExaminationProgressReportContentInterface[];
  setExaminationProgressExamPerformanceReportContent: (
    value: ExaminationProgressReportContentInterface[]
  ) => void;

  // FOR EXCEL CANDIDATE EXAM PERFORMANCE REPORT CONTENT ===============================
  candidateExamPerformanceToPrint:
    | CandidateExamPerformanceToPrintInterface[]
    | null;
  candidateExamPerformanceReportContent: CandidateExamPerformanceReportContentInterface[];
  setCandidateExamPerformanceReportContent: (
    value: CandidateExamPerformanceReportContentInterface[]
  ) => void;

  // FOR EXCEL TOP 30 CANDIDATE EXAM PERFORMANCE REPORT CONTENT ===============================
  candidate30ExamPerformanceToPrint:
    | CandidateExamPerformanceToPrintInterface[]
    | null;
  candidate30ExamPerformanceReportContent: CandidateExamPerformanceReportContentInterface[];
  setCandidate30ExamPerformanceReportContent: (
    value: CandidateExamPerformanceReportContentInterface[]
  ) => void;

  // FOR CHOOSING LOCATION ===========================
  adminExamReport: ExamReportReducerType;
  SetReportDistrictId: (
    data: {
      district_id: string;
      district_name: string;
    } | null
  ) => SetReportDistrictIdAction;

  // FROM REDUX =================
  schoolLevels: schoolLevelsStoreInterface;
  courses: course[] | null;
  examsList: ExamItemResponse[] | null;
};

const ExamReportTitle: React.FC<ExamReportTitleProps> = (props) => {
  const [changeReport, setChangeReport] = useState<boolean>(false);
  const [districtModal, setDistrictModal] = useState<boolean>(false);
  const [excelModal, setExcelModal] = useState<boolean>(false);
  const [courseModal, setCourseModal] = useState<boolean>(false);

  useEffect(() => {
    if (
      props.reportTitle === reportTitleType.EXAM_DISTRICT_PERFORMANCE &&
      props.adminExamReport.reportDistrictId === null
    ) {
      setDistrictModal(true);
    }
    if (
      props.reportTitle === reportTitleType.EXAM_DISTRICT_PERFORMANCE &&
      props.adminExamReport.reportDistrictId !== null
    ) {
      setDistrictModal(false);
    }
  }, [props.adminExamReport.reportDistrictId, props.reportTitle]);

  return (
    <Fragment>
      <div className="grid md:grid-cols-12 items-center">
        <div className="col-span-1 md:col-span-8">
          <div className="mt-4 mx-4 flex flex-col gap-1">
            <span className="text-xl font-bold text-gray-700">Exam Report</span>
            <div
              className="flex flex-row gap-2 items-end cursor-pointer"
              onClick={() => setChangeReport(true)}
            >
              <span className="text-gray-500 text-xl">Report type: </span>
              <span className="text-blue-600 text-3xl font-bold underline">
                {props.reportTitle}{" "}
                {props.reportTitle ===
                  reportTitleType.EXAM_DISTRICT_PERFORMANCE &&
                props.adminExamReport.reportDistrictId
                  ? props.adminExamReport.reportDistrictId.district_name
                  : ""}
              </span>
            </div>
            {(props.reportTitle === reportTitleType.EXAM_DISTRICT_PERFORMANCE ||
              props.reportTitle ===
                reportTitleType.APPLICANTS_DISTRICT_PERFORMANCE ||
              props.reportTitle === reportTitleType.APPLICANTS_PERFORMANCE) && (
              <div className="flex flex-wrap justify-starts items-center text-xs text-gray-800 font-medium gap-1 my-1">
                <span className="text-gray-500">Filter result: </span>
                {props.adminExamReport.reportDistrictId &&
                (props.reportTitle ===
                  reportTitleType.APPLICANTS_DISTRICT_PERFORMANCE ||
                  props.reportTitle ===
                    reportTitleType.EXAM_DISTRICT_PERFORMANCE) ? (
                  <span className="px-2 py-1 rounded bg-gray-300">
                    # {props.adminExamReport.reportDistrictId.district_name}
                  </span>
                ) : (
                  <span className="px-2 py-1 rounded bg-gray-300">
                    # All district
                  </span>
                )}
                {props.adminExamReport.reportCourseId ? (
                  props.reportTitle ===
                    reportTitleType.APPLICANTS_DISTRICT_PERFORMANCE && (
                    <span className="px-2 py-1 rounded bg-gray-300">
                      #
                      {props.adminExamReport.reportCourseId.course_name +
                        " (" +
                        props.adminExamReport.reportCourseId.course_code +
                        ")"}
                    </span>
                  )
                ) : (
                  <span className="px-2 py-1 rounded bg-gray-300">
                    # All exams
                  </span>
                )}
              </div>
            )}
          </div>
        </div>
        <div className="col-span-1 md:col-span-4">
          <div className="float-right mt-3">
            {props.dataToPrint && props.dataToPrint.length > 0 && (
              <div className="flex flex-row items-center justify-end font-bold mb-2">
                <BiExport className="text-2xl text-gray-500" />
                <span className="text-gray-500">Export as</span>
                <button
                  className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 focus:outline-none`}
                  // disabled={
                  //   props.examsToPrint && props.examsToPrint.length <= 0
                  //     ? true
                  //     : false
                  // }
                  onClick={() => {
                    props.updateExamToPrint();
                    setExcelModal(true);
                    props.setGenerateReportModal({
                      reportType: "EXCEL",
                      status: true,
                    });
                  }}
                >
                  EXCEL
                </button>
                <button
                  className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 focus:outline-none ${
                    props.downloading && "cursor-wait"
                  }`}
                  onClick={() => props.downlaodPDF()}
                  disabled={props.downloading}
                >
                  PDF
                </button>
              </div>
            )}

            <div>
              <div
                onClick={() => setChangeReport(true)}
                className="bg-blue-50 py-2 px-2 border border-blue-300 text-blue-600 font-medium hover:bg-blue-100 flex flex-row items-center justify-center gap-2 text-sm float-right"
              >
                <FaEdit className="text-xl" />
                Change Report
              </div>
            </div>

            <section className="flex items-center gap-1 float-right mt-1">
              {(props.reportTitle ===
                reportTitleType.EXAM_DISTRICT_PERFORMANCE ||
                props.reportTitle ===
                  reportTitleType.APPLICANTS_DISTRICT_PERFORMANCE) && (
                <div
                  onClick={() => setDistrictModal(true)}
                  className="btn bg-blue-50 border border-blue-300 text-blue-600 cursor-pointer hover:bg-blue-100 flex flex-row items-center justify-center gap-2 text-sm "
                >
                  <BiFilterAlt className="text-xl" />
                  Filter by district
                </div>
              )}
              {(props.reportTitle === reportTitleType.APPLICANTS_PERFORMANCE ||
                props.reportTitle ===
                  reportTitleType.APPLICANTS_DISTRICT_PERFORMANCE) && (
                <div
                  onClick={() => setCourseModal(true)}
                  className="btn bg-blue-50 border border-blue-300 text-blue-600 cursor-pointer hover:bg-blue-100 flex flex-row items-center justify-center gap-2 text-sm "
                >
                  <BiFilterAlt className="text-xl" />
                  Filter by exam
                </div>
              )}
            </section>
          </div>
        </div>
      </div>

      {/* GENERATE EXCEL MODAL CONTROLLERS ============================================ */}
      {excelModal && (
        <ExamReportModals
          modalFor={ModalForTypes.generateReportModal}
          modalState={excelModal}
          changeModalState={setExcelModal}
          reportTitle={props.reportTitle}
          setGenerateReportModal={props.setGenerateReportModal}
          excelReportTitle={props.excelReportTitle}
          setExcelReportTitle={props.setExcelReportTitle}
          updateExamToPrint={props.updateExamToPrint}
          generateReportModal={props.generateReportModal}
          // EXAM PREPARATION ######################
          examsToPrint={props.examsToPrint}
          examPreparationReportContent={props.examPreparationReportContent}
          setExamPreparationReportContent={
            props.setExamPreparationReportContent
          }
          // EXAM PERFORMANCE ######################
          examPerformanceToPrint={props.examPerformanceToPrint}
          examPerformanceReportContent={props.examPerformanceReportContent}
          setExamPerformanceReportContent={
            props.setExamPerformanceReportContent
          }
          // TOP 30 EXAM PERFORMANCE ######################
          exam30PerformanceToPrint={props.exam30PerformanceToPrint}
          exam30PerformanceReportContent={props.exam30PerformanceReportContent}
          setExam30PerformanceReportContent={
            props.setExam30PerformanceReportContent
          }
          // EXAMINATION PROGRESS ######################
          examinationProgressToPrint={props.examinationProgressToPrint}
          examinationProgressReportContent={
            props.examinationProgressReportContent
          }
          setExaminationProgressExamPerformanceReportContent={
            props.setExaminationProgressExamPerformanceReportContent
          }
          // CANDIDATE EXAM PERFORMANCE ######################
          candidateExamPerformanceToPrint={
            props.candidateExamPerformanceToPrint
          }
          candidateExamPerformanceReportContent={
            props.candidateExamPerformanceReportContent
          }
          setCandidateExamPerformanceReportContent={
            props.setCandidateExamPerformanceReportContent
          }
          candidate30ExamPerformanceToPrint={
            props.candidate30ExamPerformanceToPrint
          }
          candidate30ExamPerformanceReportContent={
            props.candidate30ExamPerformanceReportContent
          }
          setCandidate30ExamPerformanceReportContent={
            props.setCandidate30ExamPerformanceReportContent
          }
        />
      )}
      {/* END GENERATE EXCEL MODAL CONTROLLERS ============================================ */}

      {/* CHANGE REPORT TYPE MODAL CONTROLLERS ============================================ */}
      {changeReport && (
        <ExamReportModals
          modalFor={ModalForTypes.title}
          modalState={changeReport}
          reportTitle={props.reportTitle}
          changeModalState={setChangeReport}
          setReportTitle={props.setReportTitle}
          setExcelReportTitle={props.setExcelReportTitle}
        />
      )}
      {/* END CHANGE REPORT TYPE MODAL CONTROLLERS ============================================ */}

      {/* CHANGE DISTRICT FILTER MODAL CONTROLLERS ============================================ */}
      {districtModal && (
        <ExamReportModals
          modalFor={ModalForTypes.choose_district}
          modalState={districtModal}
          changeModalState={setDistrictModal}
          districtId={
            props.adminExamReport.reportDistrictId
              ? props.adminExamReport.reportDistrictId.district_id
              : "all"
          }
          reportTitle={props.reportTitle}
        />
      )}
      {/* END CHANGE DISTRICT FILTER MODAL CONTROLLERS ============================================ */}

      {/* CHANGE COURSE FILTER MODAL CONTROLLERS ============================================ */}
      {courseModal &&
        loadExaminationProgress(
          props.adminExamReport.DoneExam,
          props.examsList,
          props.schoolLevels.selectedEducationCategory,
          props.courses,
          props.schoolLevels.school_levels,
          props.thisPeriod
        ) && (
          <ExamReportModals
            modalFor={ModalForTypes.choose_exam}
            modalState={courseModal}
            AllExamData={
              loadExaminationProgress(
                props.adminExamReport.DoneExam,
                props.examsList,
                props.schoolLevels.selectedEducationCategory,
                props.courses,
                props.schoolLevels.school_levels,
                props.thisPeriod
              )!.finished
            }
            changeModalState={setCourseModal}
            thisExam={props.adminExamReport.reportCourseId}
          />
        )}
      {/* END CHANGE COURSE FILTER MODAL CONTROLLERS ============================================ */}
    </Fragment>
  );
};

const mapStateToProps = ({
  adminExamReport,
  schoolLevels,
  course,
  prepareExam,
}: StoreState): {
  schoolLevels: schoolLevelsStoreInterface;
  courses: course[] | null;
  examsList: ExamItemResponse[] | null;
  adminExamReport: ExamReportReducerType;
} => {
  return {
    schoolLevels,
    courses: course.courses,
    examsList: prepareExam.examsList,
    adminExamReport,
  };
};

export default React.memo(
  connect(mapStateToProps, { SetReportDistrictId })(ExamReportTitle)
);
