import React, { Component } from "react";
import { candidatePerfomranceStateInterface } from "../../Admin/ExamReport/CandidatePerformance";
import VBarChart from "../ChartComponents/VBarChart";

type ExamPerformanceChartProps = {
  examPerformance: {
    all: candidatePerfomranceStateInterface[];
    pass: candidatePerfomranceStateInterface[];
    failed: candidatePerfomranceStateInterface[];
    active: candidatePerfomranceStateInterface[];
  };
};

class ExamPerformanceChart extends Component<ExamPerformanceChartProps> {
  render() {
    return (
      <div className="my-3 max-w-full bg-white">
        <div className="flex items-center justify-between px-3">
          <span className="text-gray-600 font-semibold text-xl py-4 px-0">
            Examination performance chart
          </span>
        </div>

        <div className="mb-4 pb-2 px-3">
          <VBarChart
            labels={["Pass applicant(s)", "Failed applicant(s)"]}
            categoriesLabel={["Exam preparation progress"]}
            dataLabels={true}
            yaxisText="Exam performance"
            tooltipText="(Applicants)"
            series={[
              {
                name: "Pass applicant(s)",
                data: [this.props.examPerformance.pass.length],
              },
              {
                name: "Failed applicant(s)",
                data: [this.props.examPerformance.failed.length],
              },
            ]}
            colors={["#469769", "#d97724"]}
            defaultBar={{
              borderRadius: 4,
              horizontal: false,
              columnWidth: "95%",
              endingShape: "rounded",
            }}
            chartHeight={550}
          />
        </div>
      </div>
    );
  }
}

export default React.memo(ExamPerformanceChart);
