export const examPreparationIsChangeAllowed = (
  key: examPreparationReportContentKeyInterface
): boolean => {
  if (
    key === examPreparationReportContentKeyInterface.course_name ||
    key === examPreparationReportContentKeyInterface.course_code
  ) {
    return false;
  }
  return true;
};
