export const getLevelbyId = (
  school_levels: school_level[] | null,
  level_id: string
): school_level | undefined => {
  if (school_levels) {
    return school_levels.find((item) => item.school_level_id === level_id);
  }
};

export const loadCoursesByInstitute = (
  thisEducation: education_category | null,
  courseList: course[] | null,
  school_levels: school_level[] | null
): course[] | null => {
  let result: course[] | null = [],
    defaultValue: boolean = false;
  if (courseList && thisEducation) {
    for (const key in courseList) {
      if (Object.prototype.hasOwnProperty.call(courseList, key)) {
        if (courseList[key].school_levels.length > 0) {
          courseList[key].school_levels.forEach((level) => {
            let levelDetail = getLevelbyId(school_levels, level);
            if (
              levelDetail &&
              (levelDetail.education_institute.includes(
                thisEducation.education_institute_id
              ) ||
                levelDetail.education_institute.length <= 0)
            ) {
              result && result.push(courseList![key]);
            }
          });
        } else {
          result && result.push(courseList![key]);
        }
      }
    }
  }
  // RETURN DEFAULT VALUES ====================
  else {
    defaultValue = true;
  }
  return defaultValue ? courseList : result;
};

export const loadExaminationProgress = (
  DoneExamList: ExamDoneAndRemainingByPeriodInterface[] | null,
  examsList: ExamItemResponse[] | null,
  thisEducation: education_category | null,
  courseList: course[] | null,
  school_levels: school_level[] | null,
  period: string
): {
  all: examProgressStateInterface[];
  finished: examProgressStateInterface[];
  remaining: examProgressStateInterface[];
} | null => {
  let allData: examProgressStateInterface[] = [],
    completeExams: examProgressStateInterface[] = [],
    remainingExams: examProgressStateInterface[] = [],
    dataFound: boolean = false;
  if (
    DoneExamList &&
    loadCoursesByInstitute(thisEducation, courseList, school_levels) &&
    examsList
  ) {
    dataFound = true;
    allData = FC_reportDoneExamList(
      loadCoursesByInstitute(thisEducation, courseList, school_levels)!,
      examsList,
      DoneExamList
    ).filter((item) => item.exam && item.exam.period === period);
    completeExams = allData.filter(
      (item) => item.preparation_status.toLocaleLowerCase() === "done"
    );
    remainingExams = allData.filter(
      (item) => item.preparation_status.toLocaleLowerCase() === "active"
    );
  }
  console.log("====================================");
  console.log({ loadExaminationProgress: DoneExamList });
  console.log("====================================");
  return dataFound
    ? { all: allData, finished: completeExams, remaining: remainingExams }
    : null;
};

export const loadExamPreparationProgress = (
  dataList: examPreparationStateInterface[]
): {
  all: examPreparationStateInterface[];
  finished: examPreparationStateInterface[];
  remaining: examPreparationStateInterface[];
} => {
  let allData: examPreparationStateInterface[] = dataList,
    preparedExams: examPreparationStateInterface[] = dataList.filter(
      (item) => item.preparation_status === "prepared"
    ),
    remainingExams: examPreparationStateInterface[] = dataList.filter(
      (item) => item.preparation_status === "not prepared"
    );
  return { all: allData, finished: preparedExams, remaining: remainingExams };
};

export const isDatePast = (dateParam: string): boolean => {
  const date = moment().format("DD-MM-YYYY");
  const todayDate = moment(date, "DD-MM-YYYY");
  const pastDate = moment(dateParam, "DD-MM-YYYY");

  const dDiff = todayDate.diff(pastDate);

  if (dDiff > 0) {
    return false;
  } else {
    return true;
  }
};

export const loadExamAttendance = (
  applicantList: candidatePerfomranceStateInterface[],
  period: string
): {
  all: candidatePerfomranceStateInterface[];
  pass: candidatePerfomranceStateInterface[];
  failed: candidatePerfomranceStateInterface[];
  active: candidatePerfomranceStateInterface[];
} => {
  return {
    all: applicantList.filter((item) => item.exam.period === period),
    pass: applicantList.filter(
      (item) =>
        item.exam.period === period &&
        (item.exam_stastus !== "Editing" || isDatePast(item.exam.exam_date)) &&
        item.user_total_marks >= 50
    ),
    failed: applicantList.filter(
      (item) =>
        item.exam.period === period &&
        (item.exam_stastus !== "Editing" || isDatePast(item.exam.exam_date)) &&
        item.user_total_marks < 50
    ),
    active: applicantList.filter(
      (item) =>
        item.exam.period === period &&
        item.exam_stastus === "Editing" &&
        !isDatePast(item.exam.exam_date)
    ),
  };
};

