export const getExamByCourseId = (
  allExams: ExamItemResponse[],
  course_id: string
): ExamItemResponse | undefined => {
  return allExams.find((item) => item.course === course_id);
};

export const getPeriodById = (
  periods: SessionPlanInterface[],
  period_id: string
): SessionPlanInterface | undefined => {
  return periods.find((item) => item._id === period_id);
};

export const loadAllExams = (
  examsList: ExamItemResponse[] | null,
  selected_period: SessionPlanInterface | null
): ExamItemResponse[] | null => {
  let result: ExamItemResponse[] | null = examsList;
  if (result && selected_period) {
    result = result.filter((item) => item.period === selected_period?._id);
  } else {
    result = examsList;
  }
  return result;
};

// TYPES OF REPORTS ======================================
export enum reportTitleType {
  APPLICANTS_PERFORMANCE = "Exam report",
  EXAM_PREPARATION = "List of prepared and not prepared exams",
  EXAM_PERFORMANCE = "Exam performance",
  EXAMINATION_PROGRESS = "List of complete & remaining exams",
  EXAM_DISTRICT_PERFORMANCE = "Top 30 exams passed in", // ADD DISTRICT NAME EX: Muhanga District
  APPLICANTS_DISTRICT_PERFORMANCE = "Top 30 applicants performance", // ADD DISTRICT NAME EX: Muhanga District && EXAM NAME EX: Physics
}

