export interface examPerformanceReportContentInterface {
  key: examPerformanceReportContentKeyInterface;
  title: string;
  status: boolean;
}

export const examPerformanceIsChangeAllowed = (
  key: examPerformanceReportContentKeyInterface
): boolean => {
  if (
    key === examPerformanceReportContentKeyInterface.COURSER_NAME ||
    key === examPerformanceReportContentKeyInterface.COURSE_CODE ||
    key === examPerformanceReportContentKeyInterface.PERIOD ||
    key === examPerformanceReportContentKeyInterface.PERFORMANCE
  ) {
    return false;
  }
  return true;
};

