export interface examPerformanceStateInterface {
  course_name: string;
  course_code: string;
  description: string;
  school_levels: string[];
  course_id: string;
  exam: ExamItemResponse | null;
  exam_performance: number;
}

export const FC_reportExamPerformanceData = (
  allCourses: course[],
  allExams: ExamItemResponse[],
  examPerformance: ExamsParcentageSuccessFailedRateByPeriodInterface[]
): examPerformanceStateInterface[] => {
  let result: examPerformanceStateInterface[] = [],
    ids: string[] = [];
  for (const key in examPerformance) {
    if (Object.prototype.hasOwnProperty.call(examPerformance, key)) {
      if (ids.includes(examPerformance[key].answer_sheet_id)) continue;
      ids.push(examPerformance[key].answer_sheet_id);
      let courser = allCourses.find(
        (item) => item.course_id === examPerformance[key].course
      );
      if (courser) {
        let exam = allExams.find(
          (item) =>
            item.course === examPerformance[key].course &&
            item.period === examPerformance[key].period
        );
        result.push({
          course_name: courser.course_name,
          course_code: courser.course_code,
          description: courser.description,
          school_levels: courser.school_levels,
          course_id: courser.course_id,
          exam: exam ? exam : null,
          exam_performance: examPerformance[key].total_marks,
        });
      }
    }
  }
  console.log("===============-----------------=====================");
  console.log({ result });
  console.log("===============-----------------=====================");
  return result;
};

export const searchExamPerformanceData = (
  objList: examPerformanceStateInterface[],
  text: string
): examPerformanceStateInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "course_name" ||
        prop === "course_code" ||
        prop === "exam_performance"
      ) {
        flag =
          product[prop].toString().toLowerCase().indexOf(text.toLowerCase()) >
          -1;
      }
      if (flag) break;
    }
    return flag;
  });
};

type ExamPerformanceProps = {
  loading: boolean;
  examsListData: ExamItemResponse[] | null;
  courseListData: course[] | null;
  thisPeriod: SessionPlanInterface[];
  examsPerformanceData:
    | ExamsParcentageSuccessFailedRateByPeriodInterface[]
    | null;
};

const ExamPerformance: React.FC<ExamPerformanceProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  const [isSorted, setIsSorted] = useState<boolean>(false);
  const [sortProperty, setSortProperty] = useState<string>("");
  const [sortOrder, setSortOrder] = useState<"ASC" | "DESC">("ASC");

  const setSort = (thisProperty: string) => {
    if (sortProperty === thisProperty && sortOrder === "ASC") {
      setSortOrder("DESC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    } else {
      setSortOrder("ASC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    }
  };

  return (
    <React.Fragment>
      <div className="bg-white my-4">
        <div className="w-full px-2 py-2">
          <input
            onChange={(e) => setSearchInput(e.target.value)}
            type="text"
            value={searchInput}
            placeholder="Search..."
            className="w-full bg-gray-100 py-2 px-3 rounded"
            disabled={
              props.courseListData &&
              props.examsListData &&
              props.examsPerformanceData &&
              FC_reportExamPerformanceData(
                props.courseListData,
                props.examsListData,
                props.examsPerformanceData
              ).length <= 0
                ? true
                : false
            }
          />
        </div>
        <div className="w-full overflow-x-auto">
          <table className="w-full shadow" id="report_content">
            <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
              <tr>
                <td className="px-2 py-2">#</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("course_name")}
                >
                  <div className="flex items-center gap-1">
                    Course <BiSort />
                  </div>
                </td>
                <td className="px-2 py-2">Exam date</td>
                <td className="px-2 py-2">Exam time</td>
                <td className="px-2 py-2">Exam duration</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("exam_performance")}
                >
                  <div className="flex items-center gap-1">
                    Exam performance <BiSort />
                  </div>
                </td>
              </tr>
            </thead>
            <tbody>
              {props.courseListData === null || props.loading ? (
                <tr>
                  <td colSpan={6} className="px-2 py-2">
                    <Loading />
                  </td>
                </tr>
              ) : props.examsListData &&
                props.examsPerformanceData &&
                FC_reportExamPerformanceData(
                  props.courseListData,
                  props.examsListData,
                  props.examsPerformanceData
                ).length <= 0 ? (
                <tr>
                  <td colSpan={6} className="px-2 py-2 text-center">
                    No Exam list available!
                  </td>
                </tr>
              ) : (
                props.examsListData &&
                props.examsPerformanceData &&
                sortThisArray(
                  isSorted,
                  searchExamPerformanceData(
                    FC_reportExamPerformanceData(
                      props.courseListData,
                      props.examsListData,
                      props.examsPerformanceData
                    ),
                    searchInput
                  ),
                  sortProperty,
                  sortOrder
                ).map((item: examPerformanceStateInterface, i) => (
                  <tr
                    key={i + 1}
                    className={`bg-white text-gray-600 hover:bg-blue-100 hover:text-blue-700 border-b border-gray-300 hover:border-blue-500 text-sm`}
                  >
                    <td className="px-2 py-2">{i + 1}</td>
                    <td className="px-2 py-2">
                      {capitalizeFirstLetter(item.course_name)} (
                      <span className="font-semibold">{item.course_code}</span>)
                    </td>
                    <td className="px-2 py-2">
                      {item.exam ? DATE(item.exam.exam_date) : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam ? item.exam.start_time : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam ? item.exam!.duration + " min" : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {Number(item.exam_performance.toFixed(1)) + "%"}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    </React.Fragment>
  );
};

export default React.memo(ExamPerformance);
