export interface candidatePerfomranceStateInterface {
  user_id: string;
  user_name: string;
  national_id: string;
  user_email: string;
  user_phone: string;
  course_name: string;
  course_code: string;
  description: string;
  school_levels: string[];
  course_id: string;
  exam: CandidatesFailedSucceeded;
  user_total_marks: number;
  exam_stastus: ReportUserExamStatus;
}

export const FC_reportCandidatePerformanceData = (
  allCourses: course[],
  data: CandidatesFailedSucceeded[],
  course_id: string
): candidatePerfomranceStateInterface[] => {
  let result: candidatePerfomranceStateInterface[] = [],
    ids: string[] = [];
  for (const key in data) {
    if (Object.prototype.hasOwnProperty.call(data, key)) {
      if (!ids.includes(data[key]._id)) {
        ids.push(data[key]._id);
        let courser = allCourses.find(
          (item) => item.course_id === data[key].course
        );
        if (courser) {
          result.push({
            user_id: data[key].user.user_id,
            user_name:
              capitalizeFirstLetter(data[key].user.user_info.lname) +
              " " +
              capitalizeFirstLetter(data[key].user.user_info.fname),
            national_id: data[key].user.n_id ? data[key].user.n_id : "",
            user_email: data[key].user.email,
            user_phone: data[key].user.phone_number,
            course_name: courser.course_name,
            course_code: courser.course_code,
            description: courser.description,
            school_levels: courser.school_levels,
            course_id: courser.course_id,
            exam: data[key],
            user_total_marks: data[key].total_marks,
            exam_stastus: data[key].user_status,
          });
        }
      }
    }
  }

  if (course_id !== "all") {
    result = result.filter((item) => item.course_id === course_id);
  }
  return result;
};

export const FC_reportCandidatePerformanceDataFilter = (
  thisList: candidatePerfomranceStateInterface[],
  thisValue: ReportUserExamStatus | "all"
): candidatePerfomranceStateInterface[] => {
  if (thisValue === "all") {
    return thisList;
  } else {
    return thisList.filter((item) => item.exam_stastus === thisValue);
  }
};

export const searchCandidatePerformanceData = (
  objList: candidatePerfomranceStateInterface[],
  text: string
): candidatePerfomranceStateInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "course_name" ||
        prop === "course_code" ||
        prop === "user_total_marks" ||
        prop === "exam_stastus" ||
        prop === "description" ||
        prop === "user_name" ||
        prop === "user_phone" ||
        prop === "user_email" ||
        prop === "national_id"
      ) {
        flag =
          product[prop].toString().toLowerCase().indexOf(text.toLowerCase()) >
          -1;
      }
      if (flag) break;
    }
    return flag;
  });
};

export const controlRowsByStatus = (status: string): string => {
  if (status === "Done") {
    return "bg-blue-50 border-gray-300 hover:border-blue-500 border-t border-b";
  }
  if (status === "Removed") {
    return "bg-white border-red-300 hover:border-red-500 border-t border-b";
  }
  if (status === "Paused") {
    return "bg-white border-yellow-300 hover:border-yellow-500 border-t border-b";
  }
  return "bg-white border-gray-300 hover:border-blue-500 border-t border-b";
};

type CandidatePerformanceProps = {
  loading: boolean;
  examsListData: ExamItemResponse[] | null;
  courseListData: course[] | null;
  thisPeriod: SessionPlanInterface[];
  candidatePerformanceData: CandidatesFailedSucceeded[] | null;

  // redux props ============
  adminExamReport: ExamReportReducerType;
  // FOR APPLICANT PERFORMANCE PROPS
  filterApplicantPerformance: ReportUserExamStatus | "all";
};

const CandidatePerformance: React.FC<CandidatePerformanceProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  const [isSorted, setIsSorted] = useState<boolean>(false);
  const [sortProperty, setSortProperty] = useState<string>("");
  const [sortOrder, setSortOrder] = useState<"ASC" | "DESC">("ASC");

  const setSort = (thisProperty: string) => {
    if (sortProperty === thisProperty && sortOrder === "ASC") {
      setSortOrder("DESC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    } else {
      setSortOrder("ASC");
      setSortProperty(thisProperty);
      setIsSorted(true);
    }
  };

  return (
    <Fragment>
      <div className="bg-white my-4">
        <div className="w-full px-2 py-2">
          <input
            onChange={(e) => setSearchInput(e.target.value)}
            type="text"
            value={searchInput}
            placeholder="Search..."
            className="w-full bg-gray-100 py-2 px-3 rounded"
            disabled={
              props.courseListData &&
              props.candidatePerformanceData &&
              FC_reportCandidatePerformanceDataFilter(
                FC_reportCandidatePerformanceData(
                  props.courseListData,
                  props.candidatePerformanceData,
                  props.adminExamReport.reportCourseId
                    ? props.adminExamReport.reportCourseId.course_id
                    : "all"
                ),
                props.filterApplicantPerformance
              ).length <= 0
                ? true
                : false
            }
          />
        </div>
        <div className="w-full overflow-x-auto">
          <table className="w-full shadow" id="report_content">
            <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
              <tr>
                <td className="px-2 py-2">#</td>
                <td className="px-2 py-2" onClick={() => setSort("user_name")}>
                  Applicants
                </td>
                <td className="px-2 py-2">National ID</td>
                <td className="px-2 py-2">Email</td>
                <td className="px-2 py-2">Phone</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("course_name")}
                >
                  <div className="flex items-center gap-1">
                    Exam <BiSort />
                  </div>
                </td>
                <td className="px-2 py-2">Exam period</td>
                <td className="px-2 py-2">Exam date</td>
                <td className="px-2 py-2">Exam start time</td>
                <td className="px-2 py-2">Exam end time</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => setSort("user_total_marks")}
                >
                  <div className="flex items-center gap-1">
                    Marks <BiSort />
                  </div>
                </td>
                <td className="px-2 py-2">Status</td>
              </tr>
            </thead>
            <tbody>
              {props.courseListData === null || props.loading ? (
                <tr>
                  <td colSpan={12} className="px-2 py-2">
                    <Loading />
                  </td>
                </tr>
              ) : props.candidatePerformanceData &&
                FC_reportCandidatePerformanceDataFilter(
                  FC_reportCandidatePerformanceData(
                    props.courseListData,
                    props.candidatePerformanceData,
                    props.adminExamReport.reportCourseId
                      ? props.adminExamReport.reportCourseId.course_id
                      : "all"
                  ),
                  props.filterApplicantPerformance
                ).length <= 0 ? (
                <tr>
                  <td colSpan={12} className="px-2 py-2 text-center">
                    No applicant list available!
                  </td>
                </tr>
              ) : (
                props.candidatePerformanceData &&
                sortThisArray(
                  isSorted,
                  searchCandidatePerformanceData(
                    FC_reportCandidatePerformanceDataFilter(
                      FC_reportCandidatePerformanceData(
                        props.courseListData,
                        props.candidatePerformanceData,
                        props.adminExamReport.reportCourseId
                          ? props.adminExamReport.reportCourseId.course_id
                          : "all"
                      ),
                      props.filterApplicantPerformance
                    ),
                    searchInput
                  ),
                  sortProperty,
                  sortOrder
                ).map((item: candidatePerfomranceStateInterface, i) => (
                  <tr
                    key={i + 1}
                    className={`${controlRowsByStatus(
                      item.exam_stastus
                    )} text-gray-600 hover:bg-blue-100 hover:text-blue-700 text-sm`}
                  >
                    <td className="px-2 py-2">{i + 1}</td>
                    <td className="px-2 py-2">
                      {capitalizeFirstLetter(item.user_name)}
                    </td>
                    <td className="px-2 py-2">{item.national_id}</td>
                    <td className="px-2 py-2">{item.user_email}</td>
                    <td className="px-2 py-2">{item.user_phone}</td>
                    <td className="px-2 py-2">
                      {capitalizeFirstLetter(item.course_name)} (
                      <span className="font-semibold">{item.course_code}</span>)
                    </td>
                    <td className="px-2 py-2">
                      {item.exam
                        ? getPeriodById(props.thisPeriod, item.exam.period)
                          ? getPeriodById(props.thisPeriod, item.exam.period)!
                              .session_plan_name
                          : "-"
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam.exam_date ? DATE(item.exam.exam_date) : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam.start_time
                        ? timestampToDate(item.exam.start_time).timeAMPM
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam.end_time
                        ? timestampToDate(item.exam.end_time).timeAMPM
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {item.exam_stastus !== ReportUserExamStatus.Editing
                        ? Number(item.user_total_marks.toFixed(1)) + "%"
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {capitalizeFirstLetter(displayStatus(item.exam_stastus))}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  adminExamReport,
}: StoreState): {
  adminExamReport: ExamReportReducerType;
} => {
  return {
    adminExamReport,
  };
};

export default React.memo(connect(mapStateToProps, {})(CandidatePerformance));
