import React, { Component } from "react";
import { examPreparationStateInterface } from "../../Admin/ExamReport/ExamList";
import VBarChart from "../ChartComponents/VBarChart";

type ExamPreparationChartProps = {
  examPreparationProgress: {
    all: examPreparationStateInterface[];
    finished: examPreparationStateInterface[];
    remaining: examPreparationStateInterface[];
  };
};

class ExamPreparationChart extends Component<ExamPreparationChartProps> {
  render() {
    return (
      <div className="my-3 max-w-full bg-white">
        <div className="flex items-center justify-between px-3">
          <span className="text-gray-600 font-semibold text-xl py-4 px-0">
            Exams preparation chart
          </span>
        </div>

        <div className="mb-4 pb-2 px-3">
          <VBarChart
            labels={["Remaining exams", "Prepared exams"]}
            categoriesLabel={["Exam preparation progress"]}
            dataLabels={true}
            yaxisText="Exam preparation progress"
            tooltipText="(Exams)"
            series={[
              {
                name: "Remaining exams",
                data: [this.props.examPreparationProgress.remaining.length],
              },
              {
                name: "Prepared exams",
                data: [this.props.examPreparationProgress.finished.length],
              },
            ]}
            colors={["#4b5563", "#469769"]}
            defaultBar={{
              borderRadius: 4,
              horizontal: false,
              columnWidth: "95%",
              endingShape: "rounded",
            }}
            chartHeight={550}
          />
        </div>
      </div>
    );
  }
}

export default React.memo(ExamPreparationChart);
