import React, { Fragment, useState } from "react";
import { AiFillDashboard } from "react-icons/ai";
import { MdBrightness1 } from "react-icons/md";
import { SessionPlanInterface } from "../../../interfaces";
import { education_category } from "../../../interfaces/education_category";
import { reportTitleType } from "../../Admin/ExamReport/ExamReport";
import ExamReportModals, {
  ModalForTypes,
} from "../../Admin/ExamReport/ExamReportModals";

type ExamReportTitleProps = {
  // FOR SETTINGS ==============================
  loading: boolean;

  // FOR REPORT TYPE =========================
  reportTitle: reportTitleType;
  setReportTitle: (title: reportTitleType) => void;

  //   FOR EDUCATION CATEGORY & PERIOD =====================
  education_categories: education_category[] | null;
  loadingEducation: boolean;
  thisEducation: education_category | null;
  setThisEducation: (value: education_category | null) => void;

  //   FOR APPLICATION SESSION ============================
  thisPeriod: string;
  setThisPeriod: (value: string) => void;
  application_sessions: SessionPlanInterface[];
  onPeriodChange?: (period_id: string) => void;
};

const ExamDashboardTitle: React.FC<ExamReportTitleProps> = (props) => {
  const [changeReport, setChangeReport] = useState<boolean>(false);

  return (
    <Fragment>
      <div className="mt-3 rounded flex flex-col md:flex-row md:justify-between items-start">
        <div className="flex flex-row items-center gap-3 mb-2">
          <AiFillDashboard className="text-7xl text-blue-600 hidden md:block" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-3xl font-bold">
              Exam Dashboard
            </span>
            {false && (
              <div
                onClick={() => setChangeReport(true)}
                className="flex flex-row gap-2"
              >
                <span className="text-gray-500">Report type: </span>
                <span className="text-gray-600 font-bold underline cursor-pointer">
                  {props.reportTitle}
                </span>
              </div>
            )}
          </div>
        </div>

        {/* FILTER SECTION PERIOD && EDUCATION */}
        <section className="w-full md:w-auto my-2 md:my-0">
          {/* EDUCATION CATEGORY sections */}
          <section className="flex items-start md:items-center justify-start md:justify-end gap-2">
            {props.education_categories &&
              props.education_categories.length > 0 &&
              props.education_categories.map((education, index) => (
                <section
                  key={education.education_institute_id + index}
                  className={`w-auto ${
                    props.loading || props.loadingEducation
                      ? "cursor-not-allowed"
                      : "cursor-pointer"
                  } ${
                    props.thisEducation &&
                    education.education_institute_id ===
                      props.thisEducation.education_institute_id
                      ? "bg-blue-500 text-white font-bold"
                      : "bg-gray-100 text-blue-800"
                  } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex items-center animate__animated animate__zoomIn`}
                  onClick={() =>
                    !props.loading &&
                    !props.loadingEducation &&
                    props.setThisEducation(education)
                  }
                >
                  <section className="flex-1">
                    <MdBrightness1 className="inline-block mr-1 text-xs" />
                    <span>{education.education_category_title}</span>
                  </section>

                  {/* {count > 0 ? ( */}
                  {/* <span className="px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full">
              12
            </span> */}
                  {/* ) : null} */}
                </section>
              ))}
          </section>
          {/* end EDUCATION CATEGORY sections */}
          <div>
            <select
              className={`bg-white text-gray-600 border border-gray-400 rounded p-2 focus:outline-none ${
                props.loading && "cursor-wait"
              }`}
              value={props.thisPeriod}
              disabled={props.loading || props.loadingEducation}
              onChange={(e) => {
                props.setThisPeriod(e.target.value);
                props.onPeriodChange && props.onPeriodChange(e.target.value);
                // props.setLoadingApplications(true);
                // props.GetApplicationPerPeriod(
                //   e.target.value,
                //   (status: boolean, msg: string) => {
                //     props.setLoadingApplications(status);
                //     if (!status && msg.length > 0) {
                //       props.PushPopupAlert({
                //         title: errorToText(msg),
                //         theme: PopupAlertTheme.danger,
                //       });
                //     }
                //   }
                // );
              }}
            >
              {props.application_sessions.map((session, item) => (
                <option key={session._id + item} value={session._id}>
                  {session.session_plan_name}
                </option>
              ))}
            </select>
          </div>
        </section>
        {/* END FILTER SECTION PERIOD && EDUCATION */}
      </div>
      {/* CHANGE REPORT TYPE MODAL CONTROLLERS ============================================ */}
      {changeReport && (
        <ExamReportModals
          modalFor={ModalForTypes.title}
          modalState={changeReport}
          reportTitle={props.reportTitle}
          changeModalState={setChangeReport}
          setReportTitle={props.setReportTitle}
        />
      )}
      {/* END CHANGE REPORT TYPE MODAL CONTROLLERS ============================================ */}
    </Fragment>
  );
};

export default React.memo(ExamDashboardTitle);
