import React, { Component } from "react";
import { examProgressStateInterface } from "../../Admin/ExamReport/DoneExamList";
import VBarChart from "../ChartComponents/VBarChart";

type ExamProgressChartProps = {
  progressData: {
    all: examProgressStateInterface[];
    finished: examProgressStateInterface[];
    remaining: examProgressStateInterface[];
  };
};

class ExamProgressChart extends Component<ExamProgressChartProps> {
  render() {
    return (
      <div className="my-3 max-w-full bg-white">
        <div className="flex items-center justify-between px-3">
          <span className="text-gray-600 font-semibold text-xl py-4 px-0">
            Examination progress chart
          </span>
        </div>

        <div className="mb-4 pb-2 px-3">
          <VBarChart
            labels={["Remaining exams", "Prepared exams"]}
            categoriesLabel={["Examinations progress"]}
            dataLabels={true}
            yaxisText="Examination progress"
            tooltipText="(Exams)"
            series={[
              {
                name: "Remaining exams",
                data: [this.props.progressData.remaining.length],
              },
              {
                name: "Done exams",
                data: [this.props.progressData.finished.length],
              },
            ]}
            colors={["#4b5563", "#469769"]}
            defaultBar={{
              borderRadius: 4,
              horizontal: false,
              columnWidth: "95%",
              endingShape: "rounded",
            }}
            chartHeight={550}
          />
        </div>
      </div>
    );
  }
}

export default React.memo(ExamProgressChart);
