export const FC_DistinctDistrict = (
  locations: LocationRoomTableInterface[]
): LocationRoomTableInterface[] => {
  var flags: string[] = [],
    output: LocationRoomTableInterface[] = [],
    l: number = locations.length,
    i: number;
  for (i = 0; i < l; i++) {
    if (flags.includes(locations[i].district_id)) continue;
    flags.push(locations[i].district_id);
    output.push(locations[i]);
  }

  return output;
};

