    if (
      props.reportTitle &&
      props.reportTitle === reportTitleType.EXAMINATION_PROGRESS
    ) {
      return examinationProgressIsChangeAllowed(key);
    }
    // CANDIDATE EXAM PERFORMANCE && TOP 30 CANDIDATE EXAM PERFORMANCE ######################
